import React, { useState } from 'react'

import { format, addDays, addMonths } from 'date-fns'
// import DatePicker from "react-datepicker"
// import "react-datepicker/dist/react-datepicker.css"

import { getDateObjFromDate } from '../app/utilities'

import styles from './DateInput.module.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'


export default function DateInput(props) {

  const dateformatConverted = props.format.replace(/ddd/g, 'eee')
  const [dateValue, setDateValue] = useState(props.dateValue)
  // console.log(`--->: DateInput -> props.dateValue received: `, props.dateValue)

  function onClearHandler() {
    setDateValue('')
    props.onDateChange('')
  }

  function onChangeHandler(pValue) {
    setDateValue(pValue)
    props.onDateChange(pValue)
  }

  const dateString = dateValue === '' ? dateValue : format(addMonths(addDays(getDateObjFromDate(dateValue), props.modifiers.AddDay), props.modifiers.AddMonth), dateformatConverted)


  return (
    <div className={styles.dateContainer}>
      <input
        type="date"
        name=""
        id=""
        className={styles.datePicker}
        defaultValue={dateValue}
        onChange={(e) => onChangeHandler(e.target.value)}
      />
      {dateString}
      <FontAwesomeIcon
        className={styles.btnClear}
        icon={faTimes}
        onClick={onClearHandler}
      />
    </div>
  )
}
