  const getTokenHandler = (username, password, member) => {

    const headers = new Headers()
    headers.append("Content-Type", "application/x-www-form-urlencoded")


    let urlencoded = new URLSearchParams()
    urlencoded.append("username", username)
    urlencoded.append("password", password)
    urlencoded.append("MemberCode", member)
    urlencoded.append("grant_type", "password")
    urlencoded.append("timezone", "Australia/Sydney")

    fetch(`${apiURL}oAuth/Token`, {
      method: 'POST',
      headers: headers,
      body: urlencoded
    })
      .then(res => res.text())
      .then(result => {
        const resultJSON = JSON.parse(result)
        // console.log('result: ', resultJSON)
        if (resultJSON.error) {
          setLoginErrorMsg(resultJSON.error_description)
        }
        else {
          getUser(resultJSON.access_token)
          getProjects(resultJSON.access_token)
          setToken(resultJSON)
          localStorage.setItem('dynFormToken', JSON.stringify(resultJSON))
          setIsPopinLoginVisible(false)
        }
      })
      .catch(error => console.log('error', error))
  }

  const getUser = (pToken) => {
    const userHeaders = new Headers()
    userHeaders.append("Authorization", `Bearer ${pToken}`)

    const requestOptions = {
      method: 'GET',
      headers: userHeaders
    }

    fetch(`${apiURL}V1.0/User`, requestOptions)
      .then(response => response.text())
      .then(result => setUser(JSON.parse(result)))
      .catch(error => console.log('error', error))
  }


  function getProjects(pToken) {
    const projectsHeaders = new Headers()
    projectsHeaders.append("Authorization", `Bearer ${pToken}`)
    projectsHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: projectsHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project?selectorFields=(Id,Abbr)`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log('projects: ', JSON.parse(result))
        setProjects(JSON.parse(result))
      })
      .catch(error => console.log('error', error))
  }


  /**-------------------------------------------------**
  * @desc - TEMPLATES                             -----*
  *----------------------------------------------------*/
  function getTemplates(pProjectID) {
    // console.log(`--->: getTemplates -> pProjectID: `, pProjectID)
    setIsFetchingTemplates(true)

    const templatesHeaders = new Headers()
    templatesHeaders.append("Authorization", `Bearer ${token.access_token}`)
    templatesHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: templatesHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project/${pProjectID}/Document/Form/Templates?selectorFields=(Id,Title1)`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log('templates: ', JSON.parse(result))
        setTemplates(JSON.parse(result))
        setIsFetchingTemplates(false)
      })
      .catch(error => console.log('error', error))
  }


  async function getTemplateById(pTemplateId) {
    console.log('---> getTemplateById: ', pTemplateId)
    let template

    const templateHeaders = new Headers()
    templateHeaders.append("Authorization", `Bearer ${token.access_token}`)
    templateHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: templateHeaders,
      redirect: 'follow'
    }

    await fetch(`${apiURL}V1.0/Project/${selectedProject[0].id}/Document/Form/Template/${pTemplateId}`, requestOptions)
      .then(response => response.text())
      .then(result => {
        template = JSON.parse(result)
        // console.log('template: ', template)
      })
      .catch(error => console.log('error', error))
    // setSelectedTemplate(template)
    return template

  }


  /**-------------------------------------------------**
  * @desc - DOCUMENTS                             -----*
  *----------------------------------------------------*/
  function getDocuments(pProjectID) {
    setIsFetchingDocuments(true)

    const documentsHeaders = new Headers()
    documentsHeaders.append("Authorization", `Bearer ${token.access_token}`)
    documentsHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: documentsHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project/${pProjectID}/Documents?selectorFields=Id,Title,DocumentNumber,DocumentFormTemplateId&orderBy=Id desc`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log(' ====> doucments: ', JSON.parse(result))
        setDocuments(JSON.parse(result))
        setIsFetchingDocuments(false)
      })
      .catch(error => console.log('error', error))
  }


  async function getDocumentById(pDocumentId) {
    // console.log('---> getDocumentById: ', pDocumentId)
    let document = {}

    const templateHeaders = new Headers()
    templateHeaders.append("Authorization", `Bearer ${token.access_token}`)
    templateHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: templateHeaders,
      redirect: 'follow'
    }

    await fetch(`${apiURL}V1.0/Project/${selectedProjectId}/Document/${pDocumentId}`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log('---> result fron fetch: ', result, JSON.parse(result), JSON.parse(result).Document)

        document = JSON.parse(result).Document
        // console.log('--> document from fetch: ', document)
      })
      .catch(error => console.log('error', error))
    return document
  }



