import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import { setOpenListId } from '../../features/display/displaySlice'
import { selectOpenListId } from '../../features/display/displaySlice'


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleUp, faAngleDown, faTimes, faCheck } from '@fortawesome/free-solid-svg-icons'

import { useSpring, animated, useTrail, config } from 'react-spring'

function API(props) {

  const json = props.controlJson

  const { Title, Type, Id, Value = '', Api,
    IncludePhoneNumber, IncludeEmail, IncludeTitle, IncludeBusinessNumber, IncludeAbbreviation, IncludeCode } = json
  const targetedApi = String(Api).toLowerCase()

  const dispatch = useDispatch()

  const [debugMode, setDebugMode] = useState(false)
  const [collapsed, setCollapsed] = useState(true)
  const [keyword, setKeyword] = useState('')
  const [list, setList] = useState([])
  const [filteredList, setFilteredList] = useState([])
  const [selectedValues, setSelectedValues] = useState((Value.constructor === String) ? [Value] : Value)

  const openListId = useSelector(selectOpenListId)

  const animatedListProps = useSpring({ height: collapsed ? 0 : 200 })
  // const animatedListProps = useSpring({height: collapsed ? 0 : 200, config: { delay: 2000 }})
  const trailPills = useTrail(selectedValues.length, {
    from: { opacity: 0, marginLeft: -12, marginTop: -6, transform: 'scaleX(0.6) scaleY(0.8)' },
    to: { opacity: 1, marginLeft: 0, marginTop: 0, transform: 'scaleX(1) scaleY(1)' },
    // config: { duration: 1250 },
    config: config.stiff
  })

  // UTILS
  function toggleDebugHandler() {
    setDebugMode(!debugMode)
  }

  // useEffect(() => {
  //   // console.log(`API (${Title}) has new json: ${JSON.stringify(json)}`)
  //   // console.log('API: default data: ', Value)
  // }, [json])

  /**-------------------------------------------------**
  * @desc - API                                   -----*
  *----------------------------------------------------*/

  const environment = 'https://api-uat.extrack.com/V1.0/'
  const projectID = 1249

  const apiUrl = `${environment}${getEndpoint()}`

  const token = JSON.parse(localStorage.getItem('dynFormToken'))
  // console.log(`--->: API -> Api`, Api, token.access_token)

  const apiHeaders = new Headers()
  apiHeaders.append("Authorization", `Bearer ${token.access_token}`)
  apiHeaders.append("Content-Type", "application/x-www-form-urlencoded")

  const requestOptions = {
    method: 'GET',
    headers: apiHeaders,
    redirect: 'follow'
  }

  function getEndpoint() {
    switch (targetedApi) {
      case 'projectcompany':
        return `Project/${projectID}/Directory/Companies`
      case 'projectcontacts':
        return `Project/${projectID}/Directory/Contacts`
      case 'globalcontacts':
        return `Org/contacts`
      case 'dockettypes':
        return `/Docket/Types`
      case 'abs':
        return `Project/${projectID}/ABS`
      case 'tbs':
        return `Project/${projectID}/TBS`
      case 'cbs':
        return `Project/${projectID}/CbsWo`
      case 'dbs':
        return `Project/${projectID}/DBS`
      case 'fbs':
        return `Project/${projectID}/FBS`
      case 'wbs':
        return `Project/${projectID}/WBS`
      case 'pcbs':
        return `Project/${projectID}/PCBS`
      case 'pbs':
        return `Project/${projectID}/PBS`
      default:
        break
    }
  }

  useEffect(() => {
    fetch(apiUrl, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log(targetedApi, JSON.parse(result))
        const dropdownOptions = JSON.parse(result)
        // console.log(`--->: API -> dropdownOptions`, dropdownOptions)

        const listItems = dropdownOptions.map((item, index) => {
          let option, phoneNumber, emailAddress, title, abn, abbr, code, spacer
          switch (Api) {

            case 'ProjectContacts':
              phoneNumber = IncludePhoneNumber ? ` | ${item.Contact.Number || `n/a`}` : ``
              emailAddress = IncludeEmail ? ` | ${item.Contact.Email || `n/a`}` : ``
              option = `${item.Contact.NameFirst} ${item.Contact.NameLast} | ${item.Company.Name}${phoneNumber}${emailAddress}`
              break

            case 'GlobalContacts':
              phoneNumber = IncludePhoneNumber ? ` | ${item.Number || `n/a`}` : ``
              emailAddress = IncludeEmail ? ` | ${item.Email || `n/a`}` : ``
              option = `${item.NameFirst} ${item.NameLast} | ${item.CompanyName}${phoneNumber}${emailAddress}`
              break

            case 'ProjectCompany':
              // console.log(`--->: API ${Api} -> item`, item)

              title = IncludeTitle ? ` | ${item.Title || `n/a`}` : ``
              abn = IncludeBusinessNumber ? ` | ${item.Company.Abn || `n/a`}` : ``
              abbr = IncludeAbbreviation ? ` | ${item.Company.Abbr || `n/a`}` : ``
              option = `${item.Company.Name}${title}${abn}${abbr}`
              // optionLabel = `${item.Company.Name} - ${item.Company.Abbr}`
              break

            case 'DocketTypes':
              option = `${item.Title}`
              // optionLabel = `${item.Title} - ${item.Code}`
              break
            default:
              title = IncludeTitle ? `${item.Title || `n/a`}` : ``
              code = IncludeCode ? `${item.Code || `n/a`}` : ``
              spacer = (IncludeCode && IncludeTitle) ? ` | ` : ``
              option = `${title}${spacer}${code}`
              // optionLabel = `${item.Title} - ${item.Code}`
              break
          }

          // if (Value !== '' && optionLabel.includes(Value)) {
          // console.log('component:', {Type: 'Api', Value, optionLabel})
          // setSelectedValue(optionValue)
          // }
          // if (Value !== '' && optionLabel.includes(Value)) {
          //   currentValue = Value
          //   console.log('API ---> DefaultValue: ', Value, '----> optionLabel : ', optionLabel)

          // }
          // return <option key={index} value={optionValue}>{optionLabel}</option>
          return option
        })
        setList(listItems)
        // console.log(`--->: API -> dropdownNodes`, dropdownNodes)

      })
      .catch(error => console.log('error', error))
  }, [])





  // AUTO CLOSE
  useEffect(() => {
    if (!collapsed) dispatch(setOpenListId(Id))
  }, [collapsed, Id, dispatch])

  useEffect(() => {
    if (openListId !== Id) setCollapsed(true)
  }, [openListId, Id])


  // FILTER THE LIST ITEMS
  useEffect(() => {
    setFilteredList(list.filter(item => item.toLowerCase().includes(keyword.toLowerCase())))
  }, [list, keyword])


  // TOGGLE THE LIST
  function listClickHandler(pEvent) {
    const target = pEvent.target
    if (target.className === 'input-select' || target.className === 'input-select-items') setCollapsed(!collapsed)
    else if (target.className === 'remove-item') {
      const targetText = target.parentNode.innerText
      if (targetText !== '') {
        updateJSON('')
        setSelectedValues(selectedValues.filter(item => item !== targetText))
      }
    }
  }
  function listIconClickHandler(pEvent) {
    setCollapsed(!collapsed)
  }

  //SET VALUE
  function listItemClickHandler(pEvent) {
    const currentItem = pEvent.target.id

    if (selectedValues[0] !== currentItem) setSelectedValues([currentItem])
    else setSelectedValues([])
    updateJSON(currentItem)
  }
  function updateJSON(pValue) {
    // console.log(`--->: updateJSON with Value: `, pValue)
    const newJson = JSON.parse(JSON.stringify(json))
    if (newJson.Value === pValue) newJson.Value = ''
    else {
      newJson.Value = pValue
      setCollapsed(true)
    }
    // console.log(`--->: updateJSON -> newJson`, newJson)
    props.updateJson(newJson)
  }

  // DISPLAY THE LIST ITEMS
  const itemList = collapsed ? '' :
    filteredList.map((item, key) => {
      const isSelected = (item === Value) ? true : false
      const iconCheck = isSelected ? <FontAwesomeIcon icon={faCheck} /> : ''
      return <div key={key} id={item} className={`input-list-item ${isSelected ? 'selected-list-item' : ''}`} onClick={listItemClickHandler}>
        {iconCheck}
        {item}
      </div>
    }
    )

  // DISPLAY THE SELECTED ITEMS
  const selectedPills = trailPills.map((props, index) => {
    const item = selectedValues[index]
    if (item !== '') return <animated.div className="input-select-item" style={props} key={index}>{item}<button id={item} className="remove-item"></button></animated.div>
    return ''
  })

  return (
    <div className="control-container">
      <div className="tag" onClick={() => toggleDebugHandler()}>
        {Type}<br />
        <span className="control-id">{Id}</span>
      </div>
      <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
      <div className="control">
        <h3>{Title}</h3>
        <div className="input-select" onClick={listClickHandler}>
          <div className="input-select-items">
            {selectedPills}
          </div>
          <FontAwesomeIcon icon={collapsed ? faAngleDown : faAngleUp} onClick={listIconClickHandler} />
        </div>
        <animated.div style={animatedListProps} className="input-list">
          <div className="input-list-searchbar">
            <input type="text" placeholder="Search" className="input-list-search" value={keyword} onChange={e => setKeyword(e.target.value)}></input>
            <FontAwesomeIcon icon={faTimes} onClick={e => setKeyword('')} />
          </div>
          <div className="input-list-items">{itemList}</div>
        </animated.div>
      </div>
    </div>
  )
}

export default API