import React from 'react'


import JsonOptions from './components/JsonOptions'
import JsonReader from './features/json/JsonReader'
import Tabulation from './components/Tabulation'
import ApiInterface from './components/ApiInterface'


import './App.scss'


function App() {

  return (
    <div className="app">
      <header className="header">
        <h1 title="v20.11.03">
          DYNAMIC FORM
        </h1>
        <ApiInterface />
      </header>
      <div className="options-container">
        <JsonOptions />
      </div>
      <Tabulation />
      <main>
        <JsonReader />
      </main>
    </div>
  )
}

export default App
