import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  selectJson
} from '../../features/json/jsonSlice';
import {
  callSection
} from '../../features/tabulation/tabulationSlice';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronRight } from '@fortawesome/free-solid-svg-icons'

function DocumentTableOfContents(props) {
  const json = useSelector(selectJson)
  const dispatch = useDispatch()

  // const { Title, Id, Elements }  = json

  function gotToSectionHanlder(pSectionIndex) {
    // console.log('pSectionIndex: ', pSectionIndex)
    dispatch(callSection(pSectionIndex))
  }

  const sections = json.Sections && json.Sections.length > 0 ? json.Sections.map((element, index) => {
    if (element.Elements[0].Type === 'Document.TableOfContents') return null
    return (
      <li key={index}>
        <button className="btn btn-secondary-outlined btn-link fixed-large" onClick={() => gotToSectionHanlder(index)}>
          {element.Title}
          <FontAwesomeIcon icon={faChevronRight} />
        </button>
      </li>
    )
  }) : ''

  return (
    <ul className="toc-list">
      {sections}
    </ul>
  )
}

export default DocumentTableOfContents
