import React, { useState } from 'react'

import  { useSelector, useDispatch } from 'react-redux'
import { setDocumentStatus } from '../../features/json/jsonSlice'


function ButtonSubmitRegister(props) {

  const json = props.controlJson
  const { Title, Id, IsVisible = true }  = json
  // console.log(`--->: Text -> IsVisible`, IsVisible)

  const dispatch = useDispatch()

  function toggleDebugHandler() {
    setDebugMode(!debugMode)
  }

  function onSubmitToRegisterHandler(pEvent) {
    // console.log('onSubmitToRegisterHandler: ', pEvent )
    dispatch(setDocumentStatus('tosend'))
  }

  // const [value, setValue] = useState(json.Value || '')
  const [debugMode, setDebugMode] = useState(false)

  return (
    IsVisible ?
    <div className="control-container">
      <div className="tag" onClick={() => toggleDebugHandler()}>
        ButtonSubmitRegister<br />
        <span className="control-id">{Id}</span>
      </div>
      <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
      <div className="control">
        <button className="btn btn-secondary" onClick={onSubmitToRegisterHandler}>
          {Title}
        </button>
      </div>
    </div>
    :
    ''
  )
}

export default ButtonSubmitRegister
