import React from 'react'
import styles from './Modal.module.scss'

import { useSpring, animated, config } from 'react-spring'


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faExclamationTriangle, faTimes } from '@fortawesome/free-solid-svg-icons'

export default function Modal(props) {

  const { visibility, toggleVisibility, message, css, backdrop } = props
  const springBackdropProps = useSpring(
    {
      opacity: visibility ? 1 : 0,
      from: { opacity: 0, zIndex: 10 },
      config: config.gentle
    }
  )
  const springModalProps = useSpring(
    {
      opacity: visibility ? 1 : 0,
      top: visibility ? 0 : -25,
      from: { opacity: 0 },
      config: config.stiff
    }
  )

  function onClickBackdropHandler(pEvent) {
    if (pEvent.target.id === 'backdrop') toggleVisibility()
  }

  const backdropStyle = backdrop ? { backgroundColor: backdrop } : ''

  return (
    <animated.div style={springBackdropProps}>
      <div
        id="backdrop"
        className={`${styles.modalBackdrop} ${visibility ? '' : styles.modalBackdrop_freeze}`}
        onClick={onClickBackdropHandler}
        style={backdropStyle}>
        <animated.div style={springModalProps}>
          <div
            className={styles.modalContainer}
            style={css}>
            <div className={styles.modalClose} onClick={toggleVisibility}>
              <FontAwesomeIcon icon={faTimes} />
            </div>
            <div className={styles.modalHeader}>
              <FontAwesomeIcon icon={faExclamationTriangle} />
            </div>
            <div className={styles.modalContent}>
              {message}
            </div>
            <div className={styles.modalFooter}>
              <button className={styles.modalBtn} onClick={toggleVisibility}>OK</button>
            </div>
          </div>
        </animated.div>
      </div>
    </animated.div>
  )
}
