import React, { useState } from 'react'

function Label(props) {

  const json = props.controlJson
  const { Title, Id, IsVisible = true, FontAttributes, FontSize = 14, HasUnderLine = false } = json

  const [debugMode, setDebugMode] = useState(false)

  function toggleDebugHandler() {
    setDebugMode(!debugMode)
  }

  // const underline = HasUnderLine ? <hr /> : ''
  return (
    IsVisible ?
      <div className="control-container">
        <div className="tag" onClick={() => toggleDebugHandler()}>
          Label<br />
          <span className="control-id">{Id}</span>
        </div>
        <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" defaultValue={JSON.stringify(json)}></textarea>
        <div className="control" style={{
          fontSize: FontSize - 2 + 'px',
          fontWeight: (FontAttributes === 'Bold' ? 'bold' : ''),
          fontStyle: (FontAttributes === 'Italic' ? 'italic' : ''),
          textDecoration: HasUnderLine ? 'underline' : ''
        }}>
          {Title}
        </div>
      </div>
      :
      ''
  )
}

export default Label