import React, { useState, useEffect } from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons'

import { addToDocumentExport } from '../../app/utilities'

function Trilean(props) {

  const json = props.controlJson
  const { Type, Title, Id, ShowComment, HideNa, IsVisible = true, Collapsible, IsCollapsed, EntryTitle, Details, Value = '', CommentValue = '' } = json
  // console.log(`--->: Trilean -> IsVisible`, IsVisible)

  const [collapsed, setCollapsed] = useState(IsCollapsed)

  // const [value, setValue] = useState(json.Value || '')
  const [debugMode, setDebugMode] = useState(false)

  function toggleDebugHandler() {
    setDebugMode(!debugMode)
  }

  function onChangeHandler(pEvent) {

    const trileanValue = pEvent.target.value
    const valuesToExport = { Type, Id, Value: trileanValue }
    addToDocumentExport(valuesToExport)

    const newJson = JSON.parse(JSON.stringify(json))
    newJson.Value = trileanValue
    props.updateJson(newJson)
  }

  function onCommentChangeHandler(pEvent) {
    // console.log('pEvent.target.value: ', pEvent.target.value)

    const newJson = JSON.parse(JSON.stringify(json))
    newJson.CommentValue = pEvent.target.value
    props.updateJson(newJson)
  }

  function onToggleClickHandler(pEvent) {
    setCollapsed(!collapsed)
  }


  useEffect(() => {
    // console.log('Trilean set data :', json)
  }, [json])

  const comment = ShowComment ? <input type="text" placeholder={EntryTitle} value={CommentValue} onChange={onCommentChangeHandler}></input> : ''

  const details = Collapsible ? <div className="control-details-container"><div className="control-details-toggle" onClick={onToggleClickHandler}><FontAwesomeIcon icon={collapsed ? faAngleDown : faAngleUp} /></div><div id="controlDetails" className={`${collapsed ? 'hidden' : ''} control-details-details`}>{Details}</div></div> : ''

  return (
    IsVisible ?
      <div className="control-container">
        <div className="tag" onClick={() => toggleDebugHandler()}>
          Trilean<br />
          <span className="control-id">{Id}</span>
        </div>
        <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
        <div className="control">
          {Title}
          <div className="control-row">
            {details}
          </div>
          <div className="control-row">
            <div className="control-inline">
              <div className="control-checkbox">
                <label htmlFor="yes">Yes</label>
                <input type="radio" id="yes" name={Id} value="Y" checked={Value === 'Y'} onChange={onChangeHandler} />
              </div>
              <div className="control-checkbox">
                <label htmlFor="no">No</label>
                <input type="radio" id="no" name={Id} value="N" checked={Value === 'N'} onChange={onChangeHandler} />
              </div>
              <div className={`${HideNa ? 'hidden' : ''} control-checkbox`}>
                <label htmlFor="na">N/A</label>
                <input type="radio" id="na" name={Id} value="NA" checked={Value === 'NA'} onChange={onChangeHandler} />
              </div>
            </div>
          </div>
          <div className="control-row">
            {comment}
          </div>
        </div>
      </div>
      :
      ''
  )
}

export default Trilean
