import React, { useState } from 'react'

import { format } from 'date-fns'

import { getDateObjFromDate } from '../app/utilities'

import styles from './DateTimeInput.module.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'

export default function DateTimeInput(props) {

  const dateFormatConverted = props.format.replace(/ddd/g, 'eee').replace('tt', 'a')
  // console.log(`--->: DateTimeInput -> dateFormatConverted`, dateFormatConverted)

  const dateTimeValue = props.dateTimeValue
  // console.log(`--->: DateTimeInput -> dateTimeValue: `, dateTimeValue)

  let dateValue = (dateTimeValue !== '') ? `${format(new Date(dateTimeValue), 'yyyy-MM-dd')}` : format(new Date(), 'yyyy-MM-dd')
  let timeValue = (dateTimeValue !== '') ? `${new Date(dateTimeValue).toLocaleTimeString()}` : new Date().toLocaleTimeString()

  function onDateChangeHandler(pEvent) {
    // console.log('onDateChangeHandler pEvent.target.value: ', pEvent.target.value, timeValue)
    dateValue = pEvent.target.value
    props.onDateTimeChange(pEvent.target.value + ' ' + timeValue)
  }
  function onTimeChangeHandler(pEvent) {
    // console.log('onTimeChangeHandler pEvent.target.value', dateValue, pEvent.target.value)
    timeValue = pEvent.target.value
    props.onDateTimeChange(dateValue + ' ' + pEvent.target.value)
  }

  function onClearHandler() {
    dateValue = ''
    timeValue = ''
    props.onDateTimeChange('')
  }

  const dateTimeStringValue = (dateTimeValue !== '') ? format(new Date(dateTimeValue), dateFormatConverted) : ''

  return (
    <div className={styles.dateTimeContainer}>
      <div>
        <input type="date" className={styles.datePicker} name="" id="" onChange={onDateChangeHandler} />
        <input type="time" className={styles.timePicker} name="" id="" onChange={onTimeChangeHandler} />
      </div>
      {dateTimeStringValue}
      <FontAwesomeIcon
        className={styles.btnClear}
        icon={faTimes}
        onClick={onClearHandler}
      />
    </div>
  )
}
