import React from 'react'
import { useSelector } from 'react-redux'

import {
  selectDebugMode
} from '../../features/display/displaySlice'

function TrileanLabel(props) {

  const json = props.controlJson
  const { Type, Title, Id, IsVisible = true, Buttons, CommentValue, HasUnderLine, ShowComment, HideNa, Value } = json

  const debugMode = useSelector(selectDebugMode)

  function onChangeHandler(pEvent) {
    // console.log('---> radio click: ', pEvent.target.value)

    const newJson = JSON.parse(JSON.stringify(json))
    newJson.Value = pEvent.target.value
    props.updateJson(newJson)
  }


  const buttons = Buttons !== undefined ? Buttons.map((button, index) => {
    const computedBackground = Value === button.Button ? button.BackgroundColorSelected : button.BackgroundColor
    if (!(HideNa && button.Button === 'NA')) return (

      <React.Fragment key={`${Id}${index}`}>

        <input type="radio" name={Id} id={`${Id}${button.Label}`} value={button.Button} className="radiocheck-hidden" onChange={onChangeHandler} />
        <label htmlFor={`${Id}${button.Label}`} className="radiocheck-button" style={{ backgroundColor: computedBackground }}>
          {button.Label}
        </label>

      </React.Fragment>
    )
  }) : ''

  const comment = ShowComment ? <div className="control-row">{CommentValue}</div> : ''
  const underline = HasUnderLine ? <hr /> : ''

  return (
    IsVisible ?
      <div className="control-container">
        <div className={`tag ${debugMode ? '' : 'hidden'}`}>
          {Type}<br />
          <span className="control-id">{Id}</span>
          <textarea name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
        </div>
        <div className="control">
          <h3>{Title}</h3>
          {comment}
          <div className="control-row">
            {buttons}
            {underline}
          </div>
        </div>

      </div>
      :
      ''
  )

}
export default TrileanLabel
