import React from 'react'
import { useDispatch, useSelector } from 'react-redux'

import {
  selectedTabulation,
  callSection,
  prevSection,
  nextSection,
} from '../features/tabulation/tabulationSlice'
import { selectJson } from '../features/json/jsonSlice'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStepBackward, faStepForward, faFastBackward, faFastForward } from '@fortawesome/free-solid-svg-icons'
import styles from './Tabulation.module.scss'

function Tabulation() {

  const dispatch = useDispatch()

  const tabIndex = useSelector(selectedTabulation)
  const json = useSelector(selectJson)

  // const tabs = (json.Sections) ? json.Sections.map((item, index) => <div key={index}>{item.Title}</div>) : ''
  const tabLength = (json.Sections) ? json.Sections.length : 0
  const tabTitle = (json.Sections) ? json.Sections[tabIndex].Title : ''

  // React.useEffect(() => {
  //   console.log('TabulationUpdated! : ',json);
  // })

  function onFirstSectionHandler(e) {
    dispatch(callSection(0))
  }
  function onPrevSectionHandler(e) {
    if (tabIndex > 0) dispatch(prevSection())
  }
  function onNextSectionHandler(e) {
    if (tabIndex < tabLength - 1) dispatch(nextSection())
  }
  function onLastSectionHandler(e) {
    dispatch(callSection(tabLength - 1))
  }

  return (
    <nav className={styles.row}>
      <button className="btn btn-primary" disabled={tabLength === 0 || tabIndex === 0} onClick={(e) => onFirstSectionHandler(e)}>
        <FontAwesomeIcon icon={faFastBackward} />
      </button>
      <button className="btn btn-primary" disabled={tabLength === 0 || tabIndex === 0} onClick={(e) => onPrevSectionHandler(e)}>
        <FontAwesomeIcon icon={faStepBackward} />
      </button>
      <div className={styles.title}>
        <h2>{tabTitle}</h2>
      </div>
      <button className="btn btn-primary" disabled={tabLength === 0 || tabIndex === tabLength - 1} onClick={(e) => onNextSectionHandler(e)}>
        <FontAwesomeIcon icon={faStepForward} />
      </button>
      <button className="btn btn-primary" disabled={tabLength === 0 || tabIndex === tabLength - 1} onClick={(e) => onLastSectionHandler(e)}>
        <FontAwesomeIcon icon={faFastForward} />
      </button>
    </nav>
  )
}

export default Tabulation
