import { createSlice } from '@reduxjs/toolkit';

export const displaySlice = createSlice({
  name: 'display',
  initialState: {
    value: [],
    debugMode: false,
    errorMsg: ''
  },
  reducers: {
    setControls: (state, action) => {
      state.value = action.payload
    },
    setDebugMode: (state, action) => {
      state.debugMode = action.payload
    },
    setOpenListId: (state, action) => {
      state.openListId = action.payload
    },
    setErrorMsg: (state, action) => {
      state.errorMsg = action.payload
    },
    //what's that
    clearErrorMsg: state => {
      state.errorMsg = ''
    }
  }
})

export const { setControls, setDebugMode, setOpenListId, setErrorMsg, clearErrorMsg } = displaySlice.actions

export const selectDisplayedControls = state => state.display.value;
export const selectDebugMode = state => state.display.debugMode;
export const selectOpenListId = state => state.display.openListId;
export const selectErrorMsg = state => state.display.errorMsg;

export default displaySlice.reducer;