import React, { useState } from 'react'
import styles from './Login.module.scss'

import { useSpring, animated, config } from 'react-spring'


import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes, faUser } from '@fortawesome/free-solid-svg-icons'

export default function Login(props) {

  const { visibility, toggleVisibility, css, backdrop, errormsg } = props
  const springBackdropProps = useSpring(
    {
      opacity: visibility ? 1 : 0,
      from: { opacity: 0, zIndex: 10 },
      config: config.gentle
    }
  )
  const springloginProps = useSpring(
    {
      opacity: visibility ? 1 : 0,
      top: visibility ? 0 : -25,
      from: { opacity: 0 },
      config: config.stiff
    }
  )

  function onClickBackdropHandler(pEvent) {
    if (pEvent.target.id === 'backdrop') toggleVisibility()
  }

  const backdropStyle = backdrop ? { backgroundColor: backdrop } : ''

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [member, setMember] = useState('')

  return (
    <animated.div style={springBackdropProps}>
      <div
        id="backdrop"
        className={`${styles.loginBackdrop} ${visibility ? '' : styles.loginBackdrop_freeze}`}
        onClick={onClickBackdropHandler}
        style={backdropStyle}>
        <animated.div style={springloginProps}>
          <div
            className={styles.loginContainer}
            style={css}>
            <div className={styles.loginClose} onClick={toggleVisibility}>
              <FontAwesomeIcon icon={faTimes} />
            </div>
            <div className={styles.loginHeader}>
              <FontAwesomeIcon icon={faUser} />
            </div>
            <div className={styles.loginContent}>
              <form>
                <label htmlFor="username">username</label>
                <input type="text" name="username" id="username" onChange={e => setUsername(e.target.value)} onFocus={() => props.clearError()} />
                <label htmlFor="password">password</label>
                <input type="password" name="password" id="password" onChange={e => setPassword(e.target.value)} onFocus={() => props.clearError()} />
                <label htmlFor="member">member</label>
                <input type="text" name="member" id="member" onChange={e => setMember(e.target.value)} onFocus={() => props.clearError()} />
              </form>
            </div>
            <div className={styles.loginFooter}>
              <button className="btn btn-primary" onClick={e => props.onSubmit(username, password, member)}>SUBMIT</button>
              {errormsg}
            </div>
          </div>
        </animated.div>
      </div>
    </animated.div>
  )
}
