import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  setJson,
  selectJson,
  selectDocumentStatus,
  setDocumentStatus
} from './jsonSlice'

import {
  selectDisplayedControls,
  selectDebugMode,
  selectErrorMsg,
} from '../display/displaySlice'

import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument
} from '../../components/controls/'

import { getControlName } from '../../app/utilities'

import { useSpring, animated, config } from 'react-spring'
import { Keyframes, delay } from 'react-spring/renderprops'

import styles from './JsonReader.module.scss'

export default function JsonReader() {

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument
  }

  const json = useSelector(selectJson)
  const debugMode = useSelector(selectDebugMode)
  const errorMsg = useSelector(selectErrorMsg)
  const displayedControls = useSelector(selectDisplayedControls)
  const documentStatus = useSelector(selectDocumentStatus)
  const dispatch = useDispatch()

  const activeSectionId = useSelector(state => state.tabulation.value)
  const currentJsonToEdit = (json.Sections !== undefined) ? json.Sections[activeSectionId] : {}

  const { Title, Elements } = currentJsonToEdit

  const [controls, setControls] = useState([])
  const [isLoading, setIsLoading] = useState(false)

  useEffect(() => {
    // console.log('Section Updated! ',Elements)

    if (Elements !== undefined) {
      const controlsFromElements = Elements.map((element, index) => {
        const componentName = getControlName(element.Type)
        // console.log(`--->componentName: `, componentName)
        let controlComponent
        if (components[componentName] !== undefined) {
          const Component = components[componentName]
          controlComponent = <Component key={`${componentName.substring(0, 4).toLowerCase()}-${element.ElementId}`} controlJson={element} updateJson={updateJsonHandler} />
        }
        return controlComponent
      })
      setControls(controlsFromElements)
    }
    else setControls('')
  }, [Elements])

  useEffect(() => {
    // console.log('displayedControls updated... ', displayedControls)
    let jsonToUpdate = JSON.parse(JSON.stringify(json))

    if (json.Sections !== undefined) {
      json.Sections[activeSectionId].Elements.forEach(control => {
        // console.log(`--->: JsonReader -> control`, control.Id)
        const controlPath = getControlPathById(control.Id)
        // console.log(`--->: JsonReader -> controlPath`, controlPath)
        if (jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index].IsVisible !== undefined) {
          jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index].IsVisible = false
          console.log('---> reset control visibility ', control.Id, control.isVisible)
        }
      })
    }


    displayedControls.forEach(id => {
      const controlPath = getControlPathById(id)
      jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index].IsVisible = true
      // console.log('---> ', jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index])
    })
    dispatch(setJson(jsonToUpdate))
  }, [displayedControls])

  useEffect(() => {
    // console.log('json.Sections: ', json.Sections)
  }, [json.Sections])


  /**-------------------------------------------------**
  * @desc - Utilities                             -----*
  *----------------------------------------------------*/

  // function getControlName(pControlType) {
  //   return pControlType.split('.').map(part => part[0].toUpperCase() + part.slice(1)).join('')
  // }

  function getControlPathById(pControlId) {
    // console.log(`--->: getControlPathById -> pControlId`, pControlId)
    const currentSection = json.Sections[activeSectionId]
    let foundItem = {}
    let itemPath = {}

    currentSection.Elements.forEach((item, index) => {
      if (item.hasOwnProperty('Elements')) {

        item.Elements.forEach((subitem, subindex) => {
          if (subitem.hasOwnProperty('Elements')) {

            subitem.Elements.forEach((subsubitem, subsubindex) => {
              if (subsubitem.Id === pControlId) {
                // console.log('---> Trouv subsubitem: ', subsubitem)
                itemPath = { section: activeSectionId, index, subindex, subsubindex }
                foundItem = subsubitem
              }
            })
          }

          if (subitem.Id === pControlId) {
            // console.log('---> Trouv subitem: ', subitem)
            itemPath = { section: activeSectionId, index, subindex }
            foundItem = subitem
          }
        })
      }

      if (item.Id === pControlId) {
        // console.log('---> Trouv item: ', item)
        itemPath = { section: activeSectionId, index }
        foundItem = json.Sections[activeSectionId].Elements[index]
      }
    })
    // console.log('-- itemPath -> ', itemPath)
    // return foundItem
    return itemPath
  }

  /**-------------------------------------------------**
  * @desc - JSON                                  -----*
  *----------------------------------------------------*/

  function updateJsonHandler(pJson) {
    console.log('updating... ', documentStatus)
    if (documentStatus === '') dispatch(setDocumentStatus('modified'))

    const controlPath = getControlPathById(pJson.Id)
    // console.log(`--->: updateJsonHandler -> controlPath: `, controlPath)

    let jsonToUpdate = JSON.parse(JSON.stringify(json))
    // console.log(`--->: updateJsonHandler -> jsonToUpdate`, jsonToUpdate)
    // ONLY FIST SUBLEVEL
    jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index] = pJson
    dispatch(setJson(jsonToUpdate))
  }


  const skeleton = documentStatus === 'loading' ?
    <div className="skeleton">
      {Array(8).fill('').map((item, index) => <div key={index} className={`${index % 2 === 1 ? 'skeleton-bloc-offset' : ''} skeleton-bloc`}></div>)}
    </div>
    :
    ''


  return (
    <div className="board">

      <div className={`${errorMsg.length > 0 ? '' : 'hidden'} error-msg`}>
        {errorMsg}
      </div>


      {skeleton}

      {controls}

      <textarea
        name="jsonviewer"
        id="jsonviewer"
        className={debugMode ? `${styles.fixed}` : 'hidden'}
        // className={styles.fixed}
        rows="16"
        value={JSON.stringify(Elements)}
        onChange={e => dispatch(setJson(JSON.parse(e.target.value)))}>
      </textarea>
    </div>
  )
}
