import React, { useState } from 'react'

import { getControlName } from '../../app/utilities'
import { DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  // Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument } from './'

function Repeatable(props) {

  const json = props.controlJson
  const { Title, Id, Data, Elements, IsVisible = true }  = json
  // console.log(`--->: Repeatable -> IsVisible`, IsVisible)

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument
  }



  const [debugMode, setDebugMode] = useState(false)

  function toggleDebugHandler() {
    setDebugMode(!debugMode)
  }

  function updateJsonHandler(pJson) {
    console.log('update this: ', pJson)
    console.log('in this: ', json.Elements)
    const mergedElements = [...json.Elements]

    const elementIndex = mergedElements.findIndex(element => element.ID === pJson.ID)
    mergedElements[elementIndex] = pJson
    console.log('--->elementIndex:  ', elementIndex)

    props.updateJson({...json, Elements: mergedElements})
  }

  const children = Elements.map((control, index) => {
    const componentName = getControlName(control.Type)
    // console.log(`--->componentName: `, componentName)
    const Component = components[componentName]
    return <Component key={index} controlJson={control} updateJson={updateJsonHandler} />
    // return <div key={index}>{control.Type}</div>
  })

  return (
    IsVisible ?
    <div className="control-container">
      <div className="tag" onClick={() => toggleDebugHandler()}>
        Repeatable<br />
        <span className="control-id">{Id}</span>
      </div>
      <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" defaultValue={JSON.stringify(json)}></textarea>
      <div className="control">
        {children}
      </div>
    </div>
    :
    ''
  )
}

export default Repeatable