import { createSlice } from '@reduxjs/toolkit';

export const tabulationSlice = createSlice({
  name: 'tabulation',
  initialState: {
    value: 0,
  },
  reducers: {
    callSection: (state, action) => {
      state.value = action.payload
    },
    prevSection: state => {
      state.value -= 1
    },
    nextSection: state => {
      state.value += 1
    },
  },
});

export const { callSection,
  prevSection,
  nextSection,
  } = tabulationSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state) => state.counter.value)`
export const selectedTabulation = state => state.tabulation.value;


export default tabulationSlice.reducer;