import React, { useState } from 'react'

import { format, addHours } from 'date-fns'

import { getDateObjFromTime } from '../app/utilities'

import styles from './TimeInput.module.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'

export default function TimeInput(props) {

  // console.log(`--->: TimeInput -> timeValue received: `, props.timeValue, getDateObjFromTime(props.timeValue))

  const timeformatConverted = props.format.replace('tt', 'a')
  const [timeValue, setTimeValue] = useState(props.timeValue)

  function onClearHandler(pEvent) {
    setTimeValue('')
    props.onTimeChange('')
  }

  function onChangeHandler(pValue) {
    setTimeValue(pValue)
    props.onTimeChange(pValue)
  }

  const timeString = timeValue !== '' ? format(addHours(getDateObjFromTime(timeValue), props.modifiers.AddHour), timeformatConverted)
    :
    ''

  return (
    <div className={styles.timeContainer}>
      <input
        type="time"
        name=""
        id=""
        className={styles.timePicker}
        value={timeValue}
        onChange={(e) => onChangeHandler(e.target.value)}
      />
      {timeString}
      <FontAwesomeIcon
        className={styles.btnClear}
        icon={faTimes}
        onClick={onClearHandler}
      />
    </div>
  )
}
