import React, { useState, useEffect, useRef } from 'react'
import styles from './SelectSearchable.module.scss'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleUp, faAngleDown, faTimes } from '@fortawesome/free-solid-svg-icons'


function useVisible(initialIsVisible) {
  const [isVisible, setIsVisible] = useState(initialIsVisible)
  const ref = useRef(null)

  const handleClickOutside = (event) => {
    if (ref.current && !ref.current.contains(event.target)) {
      setIsVisible(false)
    }
  }

  useEffect(() => {
    document.addEventListener('click', handleClickOutside, true)
    return () => {
      document.removeEventListener('click', handleClickOutside, true)
    }
  }, [])
  return { ref, isVisible, setIsVisible }
}


export default function SelectSearchable(props) {

  const [keyword, setKeyword] = useState('')

  const items = props.items
  const allowMultiple = props.allowMultiple


  const filteredList = keyword.length > 0 ? items.filter(item => String(item.label).toLowerCase().includes(keyword.toLowerCase())) : items

  const { ref, isVisible, setIsVisible } = useVisible(false)

  function onClickItemHandler(pEvent, pItem) {
    pEvent.stopPropagation()
    // console.log(`--->: onClickItemHandler -> pEvent.target.className: `, pEvent.currentTarget.className)
    // console.log('---> delete here: ', pEvent.currentTarget.className.includes('delete'))

    if (pEvent.currentTarget.className.includes('delete')) {
      props.onSelectItem([])
      return
    }

    if (allowMultiple) {
      const clickedItemIndex = props.selectedItems.findIndex(item => item === pItem)

      if (clickedItemIndex > -1) {
        props.onSelectItem(props.selectedItems.filter(item => item !== pItem))
      }
      else props.onSelectItem([...props.selectedItems, pItem])
    }
    else {
      props.onSelectItem([pItem])
      setIsVisible(false)
    }
  }


  return (
    <div className={styles.selectSearchable_Container}>
      <div className={styles.selectSearchable_Header} onClick={e => setIsVisible(!isVisible)}>
        <div className={styles.selectSearchable_SelectedContainer}>
          {props.selectedItems.map((item, index) => <div key={index} className={styles.selectSearchable_SelectedItem}>
            {item.label}
            <span className={styles.selectedItem_delete} onClick={(e) => onClickItemHandler(e, item)}>
              <FontAwesomeIcon icon={faTimes} />
            </span>
          </div>)}
        </div>
        <div className={styles.selectSearchable_Trigger}>
          <FontAwesomeIcon icon={isVisible ? faAngleDown : faAngleUp} />
        </div>
      </div>
      {isVisible && <div style={{ position: 'absolute' }} className={`${styles.selectSearchable_List}`} ref={ref}>
        <div className={styles.selectSearchable_SearchContainer}>
          <input type="text" value={keyword} onChange={e => setKeyword(e.target.value)}></input>
          <FontAwesomeIcon icon={faTimes} onClick={e => setKeyword('')} />
        </div>
        <div className={styles.selectSearchable_ItemsContainer}>
          {filteredList.map((item, index) => <span key={index} className={`${styles.selectItem} ${props.selectedItems.findIndex(selectedItem => item.label === selectedItem.label) > -1 ? styles.selectSearchable_SelectedItem_Selected : ''}`} onClick={(e) => onClickItemHandler(e, item)}>{item.label}</span>)}
        </div>
      </div>}
    </div>
  )
}


