import React, { useState } from 'react'

import TimeInput from '../../components/TimeInput'

function Time(props) {

  const json = props.controlJson
  const { Type, Title, Id, FormatDateTime = 'hh:mm tt', IsBlank = false, IsVisible = true, AddHour = 0, TimeValue } = json

  const defaultTime = (TimeValue === '' || TimeValue === undefined) ?
    `${new Date().getHours() < 10 ?
      '0' + new Date().getHours()
      :
      new Date().getHours()}:${new Date().getMinutes() < 10 ?
        '0' + new Date().getMinutes()
        :
        new Date().getMinutes()}`
    :
    TimeValue
  // console.log(`TimeValue: ${TimeValue} , defaultTime: ${defaultTime}`)

  const [timeValue, setTimeValue] = useState(!IsBlank ? defaultTime : '')

  function onTimeChangeHandler(pTime) {
    console.log(`--->: onTimeChangeHandler -> pTime`, pTime)
    setTimeValue(pTime)

    const newJson = JSON.parse(JSON.stringify(json))
    if (pTime !== '') newJson.TimeValue = pTime
    else delete newJson.TimeValue
    newJson.IsBlank = (pTime !== '') ? false : true
    props.updateJson(newJson)
  }


  // Debug  ----------------------------------------- //
  const [debugMode, setDebugMode] = useState(false)
  const toggleDebugHandler = () => setDebugMode(!debugMode)

  return (
    IsVisible ?
      <div className="control-container">
        <div className="tag" onClick={() => toggleDebugHandler()}>
          {Type}<br />
          <span className="control-id">{Id}</span>
        </div>
        <textarea className={debugMode ? '' : 'hidden'} name="" id="" cols="50" rows="6" value={JSON.stringify(json)} disabled></textarea>
        <div className="control">
          <h3>{Title}</h3>
          <TimeInput format={FormatDateTime} timeValue={timeValue} modifiers={{ AddHour }} onTimeChange={(value) => onTimeChangeHandler(value)} />
        </div>
      </div>
      :
      ''
  )
}

export default Time
